body {
  flex-direction: column;
  align-content: center;
  align-items: center;
  display: flex;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  width: 100%;
  height: 60px;
  background: #2e82c7;
  align-content: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

input[name="searchQuery"] {
  width: 300px;
  min-height: 42px;
  max-height: 42px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding-left: 15px;
}

button[type="submit"] {
  width: 70px;
  min-height: 42px;
  max-height: 42px;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

button[type="submit"]:hover {
  cursor: pointer;
  background-color: #e6bc86;
}

.load-more {
  color: #fff;
  background-color: #1f5c8f;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
}

.load-more:hover {
  background-color: #17476e;
}

